import "./Home.css"
import { Button, MenuItem, TextField } from "@material-ui/core";
import Categories from "../../Data/Categories";
import { useState } from "react";
import { useHistory } from "react-router";
import ErrorMessage from "../../components/ErrorMessage/ErrorMessage"


const Home = ({ name, setName, fetchQuestions }) => {

    const [category, setCategory] = useState("");
    const [difficulty, setDifficulty] = useState("");
    const [error, setError] = useState(false);

    const history = useHistory();

    const handleSubmit = () => {
        if (!category || !difficulty || !name) {
          setError(true);
          return;
        } else {
          setError(false);
          fetchQuestions(category, difficulty);
          history.push("/quiz");
        }
      };





    return (
        <div className="content">
            <div className="settings">
                <span className="settings_title">Please fill out the fields:</span>
                    <div className="settings_select">
                        {error && <ErrorMessage>Please fill all the fields</ErrorMessage>}
                        <TextField label="Enter your name" variant="outlined"  onChange={(e) => setName(e.target.value)}/>
                        <TextField select label="Select Category" variant="outlined" onChange={(e) => setCategory(e.target.value)} value={category}> 
                        {Categories.map((cat) => (
                                <MenuItem key={cat.category} value={cat.value}>
                                    {cat.category}
                                </MenuItem>
                            ))}
                        </TextField>

                        <TextField
                            select
                            label="Select Difficulty"
                            variant="outlined"
                            style={{ marginBottom: 30}}
                            onChange={(e) => setDifficulty(e.target.value)}
                            value={difficulty}
                        
                        >
                            <MenuItem key="Easy" value="easy">
                                Easy
                            </MenuItem>
                            <MenuItem key="Medium" value="medium">
                                Medium
                            </MenuItem>
                            <MenuItem key="Hard" value="hard">
                                Hard
                            </MenuItem>
                        </TextField>

                    </div>
                    <div className="settings_button">
                        <Button variant='contained' color='primary' size="large" onClick={handleSubmit}>
                            Let's start !
                        </Button>
                    </div>

            </div>
        </div>
    )
}

export default Home